$font-stack: -apple-system,
BlinkMacSystemFont,
Roboto,
"Open Sans",
"Helvetica Neue",
sans-serif;

@import "./asd.scss";

@font-face {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  src: url(../fonts/Roboto-Regular.ttf);
}

body {
  font-family: $font-stack;

  #logo {
    width: 10rem;
  }

  .container {
    img {
      width: 20rem;
    }
  }
}